* {
  box-sizing: border-box; }

::-moz-selection {
  background: #f9e455; }

::selection {
  background: #f9e455; }

html,
body {
  width: 100%;
  min-height: 100%; }

body {
  background-color: #f9f2e2;
  font-family: 'Helvetica', 'Arial', serif;
  line-height: 1.6; }

h2 {
  color: #533217; }

.button-primary {
  background-color: #a2cce2;
  border-color: #a2cce2; }

.button {
  transition: all 0.2s; }

.section {
  padding: 8rem 0 7rem;
  text-align: center; }
  @media (min-width: 750px) {
    .section {
      padding: 15rem 0 14rem; } }
  @media (min-width: 1000px) {
    .section {
      padding: 20rem 0 19rem; } }

.hero {
  padding: 8rem 0 7rem;
  text-align: center; }
  @media (min-width: 750px) {
    .hero {
      padding: 14rem 0 19rem; } }
  .hero header {
    margin-bottom: 3rem; }
  .hero hr {
    height: 3rem;
    border: none;
    background: url("/images/book.svg") center no-repeat;
    background-size: contain; }
  .hero-heading {
    font-family: 'PT Serif', serif;
    letter-spacing: 1.8px; }
  .hero-slogan {
    margin-bottom: 3.5rem;
    font-size: 2.2rem;
    letter-spacing: 0; }

@-webkit-keyframes fade_in {
  from {
    color: #533217; }
  to {
    color: rgba(83, 50, 23, 0.5); } }

@keyframes fade_in {
  from {
    color: #533217; }
  to {
    color: rgba(83, 50, 23, 0.5); } }

.cover {
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-align: left;
  font-size: 2.1rem;
  line-height: 3.3rem;
  color: #533217; }
  @media (min-width: 750px) {
    .cover {
      margin-top: -10rem; } }
  .cover-frame {
    display: inline-block;
    width: 100%; }
    @media (min-width: 1000px) {
      .cover-frame.iphone {
        display: none; } }
    .cover-frame.ipad {
      display: none; }
      @media (min-width: 1000px) {
        .cover-frame.ipad {
          display: inline-block; } }
  .cover-text {
    font-family: 'PT Serif', serif;
    overflow: hidden;
    position: absolute;
    top: 16%;
    left: 9%;
    right: 9%;
    bottom: 16.8%;
    -webkit-animation: 0.2s linear 2s 1 alternate fade_in;
            animation: 0.2s linear 2s 1 alternate fade_in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-font-smoothing: antialiased; }
    @media (min-width: 1000px) {
      .cover-text {
        top: 10%;
        left: 13%;
        right: 13%;
        bottom: 12%; } }
    .cover-text:after {
      position: absolute;
      bottom: 0;
      display: block;
      content: '';
      width: 100%;
      height: 2rem;
      background: linear-gradient(to bottom, transparent, #f9f2e2 50%, #f9f2e2); }
    .cover-text > em {
      color: #533217;
      background-color: #f9e455;
      font-style: normal; }
  .cover-page {
    max-height: 693px;
    padding: 29% 9%;
    background: url("/images/iphone.svg") no-repeat;
    background-size: contain; }

.section {
  background-color: white; }

.process-step {
  margin-bottom: 4.5rem; }
  .process-step > h4 {
    color: #533217; }

.subscribe {
  background-color: #f9f2e2; }
  .subscribe > .container {
    max-width: 450px; }
